'use client';

export default function Logo({ size = 'default', className = '', showText = true, variant = 'default' }) {
  const sizes = {
    sm: { heart: 'w-8 h-8', text: 'text-lg', gap: 'gap-2' },
    md: { heart: 'w-12 h-12', text: 'text-2xl', gap: 'gap-3' },
    default: { heart: 'w-16 h-16', text: 'text-3xl', gap: 'gap-3' },
    lg: { heart: 'w-24 h-24', text: 'text-5xl', gap: 'gap-4' },
    xl: { heart: 'w-32 h-32', text: 'text-7xl', gap: 'gap-5' }
  };
  
  const currentSize = sizes[size] || sizes.default;
  
  // Variant styles optimized for different teen personas
  const variants = {
    default: {
      heartGradient: ['#ec4899', '#a855f7', '#0ea5e9'], // Vibrant - appeals to trendy teens
      textGradient1: 'from-pink-500 via-purple-500 to-blue-500',
      textGradient2: 'from-purple-500 via-pink-500 to-cyan-500',
      glowColor: 'rgba(236, 72, 153, 0.4)'
    },
    light: {
      heartGradient: ['#f472b6', '#c084fc', '#38bdf8'],
      textGradient1: 'from-pink-400 via-purple-400 to-blue-400',
      textGradient2: 'from-purple-400 via-pink-400 to-cyan-400',
      glowColor: 'rgba(244, 114, 182, 0.3)'
    },
    dark: {
      heartGradient: ['#be185d', '#7e22ce', '#0369a1'],
      textGradient1: 'from-pink-600 via-purple-600 to-blue-600',
      textGradient2: 'from-purple-600 via-pink-600 to-cyan-600',
      glowColor: 'rgba(190, 24, 93, 0.5)'
    },
    white: {
      heartGradient: ['#ffffff', '#fce7f3', '#e0e7ff'],
      textGradient1: 'from-white via-pink-100 to-purple-100',
      textGradient2: 'from-purple-100 via-white to-blue-100',
      glowColor: 'rgba(255, 255, 255, 0.5)'
    }
  };
  
  const currentVariant = variants[variant] || variants.default;
  
  return (
    <div className={`flex items-center ${currentSize.gap} ${className} group`}>
      {/* Heart Logo - Optimized for modern appeal */}
      <div className={`relative ${currentSize.heart} group/heart`}>
        {/* Multi-layer glow effects - appeals to gaming/tech-savvy teens */}
        <div className="absolute inset-0 bg-gradient-to-br from-pink-400 via-purple-400 to-blue-400 rounded-full blur-2xl opacity-40 animate-pulse-slow group-hover/heart:opacity-60 transition-all duration-500"></div>
        <div className="absolute inset-0 bg-gradient-to-tr from-purple-400 via-pink-400 to-cyan-400 rounded-full blur-xl opacity-30 animate-pulse-slow animation-delay-2000 group-hover/heart:opacity-50 transition-all duration-500"></div>
        <div className="absolute inset-0 bg-white/20 rounded-full blur-lg opacity-20 group-hover/heart:opacity-30 transition-opacity duration-300"></div>
        
        {/* Heart Icon with enhanced gradient and modern styling */}
        <svg 
          viewBox="0 0 24 24" 
          className={`relative ${currentSize.heart} drop-shadow-2xl transform group-hover/heart:scale-110 group-hover/heart:rotate-3 transition-all duration-300`}
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
        >
          {/* Outer glow ring - appeals to bold/edgy teens */}
          <path
            d="M20.84 4.61a5.5 5.5 0 0 0-7.78 0L12 5.67l-1.06-1.06a5.5 5.5 0 0 0-7.78 7.78l1.06 1.06L12 21.23l7.78-7.78 1.06-1.06a5.5 5.5 0 0 0 0-7.78z"
            fill="url(#heartGradient)"
            stroke="url(#heartStroke)"
            strokeWidth="1.5"
            className="group-hover/heart:drop-shadow-[0_0_20px_rgba(236,72,153,0.8)] transition-all duration-300"
          />
          <defs>
            <linearGradient id="heartGradient" x1="0%" y1="0%" x2="100%" y2="100%">
              <stop offset="0%" stopColor={currentVariant.heartGradient[0]} />
              <stop offset="50%" stopColor={currentVariant.heartGradient[1]} />
              <stop offset="100%" stopColor={currentVariant.heartGradient[2]} />
            </linearGradient>
            <linearGradient id="heartStroke" x1="0%" y1="0%" x2="100%" y2="100%">
              <stop offset="0%" stopColor="#f472b6" stopOpacity="0.8" />
              <stop offset="100%" stopColor="#c084fc" stopOpacity="0.8" />
            </linearGradient>
          </defs>
        </svg>
      </div>
      
      {/* Text Logo - Optimized for maximum appeal across teen personas */}
      {showText && (
        <div className={`font-display font-black ${currentSize.text} tracking-tight relative`}>
          {/* Smart shadow layer - ensures visibility for all backgrounds */}
          <div 
            className="absolute inset-0 flex items-center pointer-events-none"
            style={{
              textShadow: '2px 2px 8px rgba(0, 0, 0, 0.3), 0 0 20px rgba(0, 0, 0, 0.2)',
              zIndex: 0,
              filter: 'blur(0.5px)'
            }}
          >
            <span className="text-transparent">Vibe</span>
            <span className="text-transparent ml-1">Match</span>
          </div>
          
          {/* Main gradient text - modern and trendy, appeals to most teens */}
          <div className="relative z-10 flex items-center">
            <span 
              className={`bg-gradient-to-r ${currentVariant.textGradient1} bg-clip-text text-transparent relative inline-block group-hover:scale-105 transition-transform duration-300`}
              style={{
                textShadow: '0 0 30px rgba(236, 72, 153, 0.3), 0 0 60px rgba(168, 85, 247, 0.2)',
                filter: 'drop-shadow(0 2px 8px rgba(0, 0, 0, 0.2))',
                WebkitTextStroke: '0.5px rgba(255, 255, 255, 0.1)',
              }}
            >
              Vibe
            </span>
            <span 
              className={`bg-gradient-to-r ${currentVariant.textGradient2} bg-clip-text text-transparent relative inline-block ml-1 group-hover:scale-105 transition-transform duration-300`}
              style={{
                textShadow: '0 0 30px rgba(168, 85, 247, 0.3), 0 0 60px rgba(14, 165, 233, 0.2)',
                filter: 'drop-shadow(0 2px 8px rgba(0, 0, 0, 0.2))',
                WebkitTextStroke: '0.5px rgba(255, 255, 255, 0.1)',
              }}
            >
              Match
            </span>
          </div>
          
          {/* Subtle underline accent - adds modern flair */}
          <div className="absolute -bottom-1 left-0 w-full h-0.5 bg-gradient-to-r from-pink-500 via-purple-500 to-blue-500 rounded-full opacity-60 group-hover:opacity-100 transition-opacity duration-300"></div>
        </div>
      )}
    </div>
  );
}
